/* eslint-disable */
/**
 * redux   state     
 * 
 */
import { createSlice } from '@reduxjs/toolkit'

// ----------------------------------------
// Redux store  
// ----------------------------------------
let user = createSlice({
    name:'user',
    initialState:'kim', //    
    // ----------------------------------------
    // Redux store  state _Primary type
    // ----------------------------------------
    // 1. state   
    reducers : {
        changeName(state){ //  state   
            //   value   
            return 'john ' + state
        }
    }
 })
// 2.   export 
export let {changeName,} = user.actions
// 3.   import  


export default user